'use client'

import type { FooterContent } from '@/lib/siteManifest'
import type { ThemeTokens } from '@/lib/styleProcessor'

interface FooterProps {
  content: FooterContent
  tokens: ThemeTokens
  businessName?: string
}

export function Footer({ content, tokens, businessName }: FooterProps) {
  return (
    <footer className={`${tokens.sectionPadding} ${tokens.bg} border-t ${tokens.border}`}>
      <div className="max-w-7xl mx-auto">
        <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
          <div className={`text-sm ${tokens.textMuted}`}>
            {content.note || `© ${new Date().getFullYear()} ${businessName || 'Company'}. Барлық құқықтар қорғалған.`}
          </div>
          {content.links && content.links.length > 0 && (
            <nav className="flex gap-6">
              {content.links.map((link, idx) => (
                <a
                  key={idx}
                  href={link.href}
                  className={`text-sm ${tokens.textMuted} hover:${tokens.text} transition-colors`}
                >
                  {link.label}
                </a>
              ))}
            </nav>
          )}
        </div>
      </div>
    </footer>
  )
}
