'use client'

import { motion } from 'framer-motion'
import type { FeaturesContent, FeatureVariant } from '@/lib/siteManifest'
import type { ThemeTokens } from '@/lib/styleProcessor'
import { StaggeredList } from '@/hooks/useStaggeredAnimation'
import { generateBentoLayout } from '@/lib/bentoEngine'
import * as Icons from 'lucide-react'

interface FeatureGridProps {
  content: FeaturesContent
  variant?: FeatureVariant
  tokens: ThemeTokens
  anchor?: string
}

const iconMap: Record<string, any> = {
  zap: Icons.Zap,
  shield: Icons.Shield,
  rocket: Icons.Rocket,
  star: Icons.Star,
  check: Icons.CheckCircle,
  sparkles: Icons.Sparkles,
  trophy: Icons.Trophy,
  target: Icons.Target,
  heart: Icons.Heart,
  code: Icons.Code,
  settings: Icons.Settings,
  lock: Icons.Lock,
}

export function FeatureGrid({ content, variant = 'grid', tokens, anchor }: FeatureGridProps) {
  if (variant === 'bento') {
    const bento = generateBentoLayout(content.items, 'ios_spatial') // Theme will be passed from parent
    return (
      <section id={anchor} className={`${tokens.sectionPadding} ${tokens.bg}`}>
        <div className="max-w-7xl mx-auto">
          <h2
            className={`text-3xl sm:text-4xl font-bold ${tokens.text} mb-12 text-center`}
            style={{ fontFamily: tokens.fontHeading }}
          >
            {content.title}
          </h2>
          <div
            className="grid gap-6"
            style={{
              gridTemplateColumns: bento.gridTemplateColumns,
              gridTemplateRows: bento.gridTemplateRows,
              gridTemplateAreas: bento.gridTemplateAreas,
            }}
          >
            <StaggeredList direction="up" staggerDelay={0.1}>
              {bento.items.map(({ area, item }, idx) => {
                const Icon = item.icon ? iconMap[item.icon] || Icons.Star : null
                return (
                  <motion.div
                    key={idx}
                    style={{ gridArea: area }}
                    className={`${tokens.radiusCard} ${tokens.surface} p-6 ${tokens.shadow} hover:${tokens.surfaceHover} transition-all ${
                      item.highlight ? tokens.glow : ''
                    }`}
                    whileHover={{ y: -4, scale: 1.02 }}
                  >
                    {Icon && (
                      <Icon className={`w-10 h-10 ${tokens.accent} mb-4`} />
                    )}
                    <h3 className={`text-xl font-semibold ${tokens.text} mb-2`}>
                      {item.title}
                    </h3>
                    <p className={`${tokens.textMuted} leading-relaxed`}>
                      {item.description}
                    </p>
                  </motion.div>
                )
              })}
            </StaggeredList>
          </div>
        </div>
      </section>
    )
  }

  if (variant === 'zigzag') {
    return (
      <section id={anchor} className={`${tokens.sectionPadding} ${tokens.bg}`}>
        <div className="max-w-7xl mx-auto">
          <h2
            className={`text-3xl sm:text-4xl font-bold ${tokens.text} mb-12 text-center`}
            style={{ fontFamily: tokens.fontHeading }}
          >
            {content.title}
          </h2>
          <StaggeredList className="space-y-12" direction="up" staggerDelay={0.15}>
            {content.items.map((item, idx) => {
              const Icon = item.icon ? iconMap[item.icon] || Icons.Star : null
              const isEven = idx % 2 === 0
              return (
                <motion.div
                  key={idx}
                  className={`flex flex-col ${isEven ? 'lg:flex-row' : 'lg:flex-row-reverse'} gap-8 items-center`}
                  whileInView={{ opacity: 1, y: 0 }}
                  initial={{ opacity: 0, y: 30 }}
                  viewport={{ once: true }}
                >
                  <div className="flex-1">
                    {Icon && (
                      <Icon className={`w-12 h-12 ${tokens.accent} mb-4`} />
                    )}
                    <h3 className={`text-2xl font-semibold ${tokens.text} mb-3`}>
                      {item.title}
                    </h3>
                    <p className={`${tokens.textMuted} leading-relaxed text-lg`}>
                      {item.description}
                    </p>
                  </div>
                  <div className={`flex-1 ${tokens.radiusCard} ${tokens.surface} p-8 ${tokens.shadow} min-h-[200px]`} />
                </motion.div>
              )
            })}
          </StaggeredList>
        </div>
      </section>
    )
  }

  // Default: grid variant
  return (
    <section id={anchor} className={`${tokens.sectionPadding} ${tokens.bg}`}>
      <div className="max-w-7xl mx-auto">
        <h2
          className={`text-3xl sm:text-4xl font-bold ${tokens.text} mb-12 text-center`}
          style={{ fontFamily: tokens.fontHeading }}
        >
          {content.title}
        </h2>
        <StaggeredList
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
          direction="up"
          staggerDelay={0.1}
        >
          {content.items.map((item, idx) => {
            const Icon = item.icon ? iconMap[item.icon] || Icons.Star : null
            return (
              <motion.div
                key={idx}
                className={`${tokens.radiusCard} ${tokens.surface} p-6 ${tokens.shadow} hover:${tokens.surfaceHover} transition-all ${
                  item.highlight ? tokens.glow : ''
                }`}
                whileHover={{ y: -4, scale: 1.02 }}
              >
                {Icon && (
                  <Icon className={`w-10 h-10 ${tokens.accent} mb-4`} />
                )}
                <h3 className={`text-xl font-semibold ${tokens.text} mb-2`}>
                  {item.title}
                </h3>
                <p className={`${tokens.textMuted} leading-relaxed`}>
                  {item.description}
                </p>
              </motion.div>
            )
          })}
        </StaggeredList>
      </div>
    </section>
  )
}
