'use client'

import { motion } from 'framer-motion'
import type { HeroContent, HeroVariant } from '@/lib/siteManifest'
import type { ThemeTokens } from '@/lib/styleProcessor'
import { MagneticButton, MagneticLink } from '@/hooks/useMagneticButton'
import * as Icons from 'lucide-react'

interface HeroProps {
  content: HeroContent
  variant?: HeroVariant
  tokens: ThemeTokens
  anchor?: string
}

export function Hero({ content, variant = 'centered', tokens, anchor }: HeroProps) {
  const iconMap: Record<string, any> = {
    sparkles: Icons.Sparkles,
    zap: Icons.Zap,
    rocket: Icons.Rocket,
    star: Icons.Star,
    trophy: Icons.Trophy,
    target: Icons.Target,
  }

  const VisualIcon = content.visual?.type === 'icon' && content.visual.value
    ? iconMap[content.visual.value] || Icons.Sparkles
    : null

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.1,
      },
    },
  }

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.6,
        ease: [0.22, 1, 0.36, 1],
      },
    },
  }

  if (variant === 'split') {
    return (
      <section id={anchor} className={`${tokens.heroPadding} ${tokens.bg}`}>
        <div className="max-w-7xl mx-auto">
          <motion.div
            variants={containerVariants}
            initial="hidden"
            animate="visible"
            className="grid lg:grid-cols-2 gap-12 items-center"
          >
            <motion.div variants={itemVariants} className="space-y-6">
              {content.badge && (
                <motion.p
                  variants={itemVariants}
                  className={`inline-flex items-center gap-2 text-sm px-4 py-2 ${tokens.radiusPill} ${tokens.surface} ${tokens.textMuted}`}
                >
                  {content.badge}
                </motion.p>
              )}
              <h1
                className={`text-4xl sm:text-5xl lg:text-6xl font-bold ${tokens.text} leading-tight`}
                style={{ fontFamily: tokens.fontHeading }}
              >
                {content.headline}
              </h1>
              <p className={`text-lg sm:text-xl ${tokens.textMuted} leading-relaxed`}>
                {content.subheadline}
              </p>
              <div className="flex flex-col sm:flex-row gap-4 pt-4">
                <MagneticLink
                  href={content.cta.href}
                  strength={0.2}
                  className={`px-6 py-3 ${tokens.radiusButton} ${tokens.accent} ${tokens.accentText} font-semibold text-center`}
                >
                  {content.cta.primary}
                </MagneticLink>
                {content.cta.secondary && (
                  <MagneticLink
                    href="#features"
                    strength={0.2}
                    className={`px-6 py-3 ${tokens.radiusButton} ${tokens.surface} ${tokens.text} font-medium text-center`}
                  >
                    {content.cta.secondary}
                  </MagneticLink>
                )}
              </div>
            </motion.div>
            <motion.div variants={itemVariants} className="relative">
              {VisualIcon ? (
                <div className={`${tokens.radiusCard} ${tokens.surface} p-12 flex items-center justify-center`}>
                  <VisualIcon className="w-24 h-24 text-sky-400" />
                </div>
              ) : content.visual?.type === 'gradient' ? (
                <div
                  className={`${tokens.radiusCard} h-96 ${tokens.bgGradient} ${tokens.glow}`}
                  style={{
                    background: content.visual.value,
                  }}
                />
              ) : (
                <div className={`${tokens.radiusCard} ${tokens.surface} h-96 flex items-center justify-center ${tokens.shadow}`}>
                  <div className={`w-32 h-32 ${tokens.accent} ${tokens.radiusCard} opacity-20`} />
                </div>
              )}
            </motion.div>
          </motion.div>
        </div>
      </section>
    )
  }

  if (variant === 'minimal') {
    return (
      <section id={anchor} className={`${tokens.heroPadding} ${tokens.bg}`}>
        <div className="max-w-4xl mx-auto text-center">
          <motion.div
            variants={containerVariants}
            initial="hidden"
            animate="visible"
            className="space-y-8"
          >
            {content.badge && (
              <motion.p
                variants={itemVariants}
                className={`inline-flex items-center gap-2 text-sm px-4 py-2 ${tokens.radiusPill} ${tokens.surface} ${tokens.textMuted}`}
              >
                {content.badge}
              </motion.p>
            )}
            <motion.h1
              variants={itemVariants}
              className={`text-5xl sm:text-6xl lg:text-7xl font-bold ${tokens.text} leading-tight`}
              style={{ fontFamily: tokens.fontHeading }}
            >
              {content.headline}
            </motion.h1>
            <motion.p
              variants={itemVariants}
              className={`text-xl sm:text-2xl ${tokens.textMuted} leading-relaxed max-w-2xl mx-auto`}
            >
              {content.subheadline}
            </motion.p>
            <motion.div variants={itemVariants} className="flex flex-col sm:flex-row gap-4 justify-center pt-6">
              <MagneticLink
                href={content.cta.href}
                strength={0.2}
                className={`px-8 py-4 ${tokens.radiusButton} ${tokens.accent} ${tokens.accentText} font-semibold`}
              >
                {content.cta.primary}
              </MagneticLink>
              {content.cta.secondary && (
                <MagneticLink
                  href="#features"
                  strength={0.2}
                  className={`px-8 py-4 ${tokens.radiusButton} ${tokens.surface} ${tokens.text} font-medium`}
                >
                  {content.cta.secondary}
                </MagneticLink>
              )}
            </motion.div>
          </motion.div>
        </div>
      </section>
    )
  }

  // Default: centered variant
  return (
    <section id={anchor} className={`${tokens.heroPadding} ${tokens.bg} relative overflow-hidden`}>
      {/* Background glow effect */}
      <div
        className="absolute inset-0 opacity-20"
        style={{
          background: `radial-gradient(circle at 50% 0%, ${tokens.accent.replace('bg-', '')} 0%, transparent 70%)`,
        }}
      />
      <div className="max-w-6xl mx-auto relative z-10">
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          className="text-center space-y-8"
        >
          {content.badge && (
            <motion.p
              variants={itemVariants}
              className={`inline-flex items-center gap-2 text-sm px-4 py-2 ${tokens.radiusPill} ${tokens.surface} ${tokens.textMuted}`}
            >
              {content.badge}
            </motion.p>
          )}
          <motion.h1
            variants={itemVariants}
            className={`text-4xl sm:text-5xl lg:text-6xl font-bold ${tokens.text} leading-tight`}
            style={{ fontFamily: tokens.fontHeading }}
          >
            {content.headline}
          </motion.h1>
          <motion.p
            variants={itemVariants}
            className={`text-lg sm:text-xl ${tokens.textMuted} leading-relaxed max-w-3xl mx-auto`}
          >
            {content.subheadline}
          </motion.p>
          <motion.div variants={itemVariants} className="flex flex-col sm:flex-row gap-4 justify-center pt-6">
            <MagneticLink
              href={content.cta.href}
              strength={0.2}
              className={`px-8 py-4 ${tokens.radiusButton} ${tokens.accent} ${tokens.accentText} font-semibold`}
            >
              {content.cta.primary}
            </MagneticLink>
            {content.cta.secondary && (
              <MagneticLink
                href="#features"
                strength={0.2}
                className={`px-8 py-4 ${tokens.radiusButton} ${tokens.surface} ${tokens.text} font-medium`}
              >
                {content.cta.secondary}
              </MagneticLink>
            )}
          </motion.div>
        </motion.div>
      </div>
    </section>
  )
}
