import type { FeatureItem } from '@/lib/siteManifest'
import type { ThemeKey } from '@/lib/siteManifest'

/**
 * Bento Engine - Generates asymmetrical CSS Grid layouts
 * Creates visually interesting, responsive layouts based on feature count
 */

export interface BentoLayout {
  gridTemplateColumns: string
  gridTemplateRows: string
  gridTemplateAreas: string
  items: Array<{
    area: string
    item: FeatureItem
    span?: { col?: number; row?: number }
  }>
}

export function generateBentoLayout(
  features: FeatureItem[],
  theme: ThemeKey
): BentoLayout {
  const count = features.length

  // For 3 features: 2x2 grid with one spanning
  if (count === 3) {
    return {
      gridTemplateColumns: 'repeat(2, 1fr)',
      gridTemplateRows: 'repeat(2, 1fr)',
      gridTemplateAreas: `
        "a a"
        "b c"
      `,
      items: [
        { area: 'a', item: features[0], span: { col: 2 } },
        { area: 'b', item: features[1] },
        { area: 'c', item: features[2] },
      ],
    }
  }

  // For 4 features: 2x2 grid
  if (count === 4) {
    return {
      gridTemplateColumns: 'repeat(2, 1fr)',
      gridTemplateRows: 'repeat(2, 1fr)',
      gridTemplateAreas: `
        "a b"
        "c d"
      `,
      items: [
        { area: 'a', item: features[0] },
        { area: 'b', item: features[1] },
        { area: 'c', item: features[2] },
        { area: 'd', item: features[3] },
      ],
    }
  }

  // For 5 features: 3x2 grid with one spanning
  if (count === 5) {
    return {
      gridTemplateColumns: 'repeat(3, 1fr)',
      gridTemplateRows: 'repeat(2, 1fr)',
      gridTemplateAreas: `
        "a a b"
        "c d e"
      `,
      items: [
        { area: 'a', item: features[0], span: { col: 2 } },
        { area: 'b', item: features[1] },
        { area: 'c', item: features[2] },
        { area: 'd', item: features[3] },
        { area: 'e', item: features[4] },
      ],
    }
  }

  // For 6 features: 3x2 grid
  if (count === 6) {
    return {
      gridTemplateColumns: 'repeat(3, 1fr)',
      gridTemplateRows: 'repeat(2, 1fr)',
      gridTemplateAreas: `
        "a b c"
        "d e f"
      `,
      items: [
        { area: 'a', item: features[0] },
        { area: 'b', item: features[1] },
        { area: 'c', item: features[2] },
        { area: 'd', item: features[3] },
        { area: 'e', item: features[4] },
        { area: 'f', item: features[5] },
      ],
    }
  }

  // Fallback: simple grid for other counts
  const cols = count <= 2 ? count : 3
  const rows = Math.ceil(count / cols)

  return {
    gridTemplateColumns: `repeat(${cols}, 1fr)`,
    gridTemplateRows: `repeat(${rows}, 1fr)`,
    gridTemplateAreas: '',
    items: features.map((item, idx) => ({
      area: String.fromCharCode(97 + idx), // a, b, c, ...
      item,
    })),
  }
}
